import { VueperSlides, VueperSlide } from 'vueperslides';
import 'vueperslides/dist/vueperslides.css';
import { useI18n } from 'vue-i18n';
import config from '../../../../sunrise.config';
import getter from '../../../../composition/getCookie';
import useCustomerTools from 'hooks/useCustomerTools';

const customizeBanner = (banners) => {
  banners.forEach(element => {
    if (window.innerWidth < 768) {
      element['image_url'] = element.gcs_url_mobile;
    } else {
      element['image_url'] = element.gcs_url_desktop;
    }
  });
  return banners;
};

export default {
  setup() {
    const { t } = useI18n({
      inheritLocale: true,
      useScope: 'local',
    });
    return {
      t,
    };
  },
  components: {
    VueperSlides,
    VueperSlide,
  },
  data: () => ({
    autoPlaying: true,
    internalAutoPlaying: true,
    slides: [],
  }),
  mounted () {
    let queryParams = `cookieID=${ getter().getCdpCookie() }`;
    const tools = useCustomerTools();
    if (tools.customer.value) {
      queryParams += '&email=' + tools.customer.value.email;
    }
    fetch(config.zeotap.bannerHost + '/get_banner' + '?' + queryParams)
      .then(res => res.json())
      .then (data => this.slides = customizeBanner(data.banners))
      .catch((err) => { 
        let data = require('../../../../data/db.json')
        this.slides = data.banners.data.banners;
        console.log(err.message)
        console.log(this.slides)
    });
  }
};
